// Your custom theme/variable overrides go here
// Body
$body-bg: #fff;
// $body-color: #ccc;
$app-min-width: 1024px;

$primary: #6b2d6d;
$secondary: #273989;
$info:  #e6d822;
$light:  #EBDEEA;
$gray: #EEE;
$dark:  darken($primary, 20%);

:root {
    --tm-primary: #{$primary};
    --tm-secondary: #{$secondary};
    --tm-info: #{$info};
    --tm-light: #{$light};
    --tm-dark: #{$dark};
    --tm-gray: #{$gray};

    --tm-environment: #59a05d;
    --tm-p2p: #9a2c6f;
    --tm-economic: #296180;
    --tm-infrastructure: #c9493e;
}

// Required parts of Bootstrap
@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins';