@import './styles/variables.scss';

// THE LEFT PANEL
// shown when there is nothing selected. 
.TM_left-panel {

    
    z-index: 9;
    left: 0px;
    // mobile
    background-color: white;
    padding: 0px 5px 10px;
    transition: all 1s;
    
    .TM_h2 {
        font-size: 1rem;;
    }
    color: black;
    // Desktop
    @media only screen and (min-width: 600px) {
        .TM_h2 {
            font-size: 1.4rem;;
        }   
        position: absolute;
        right: 50%;
        border: 1px solid var(--tm-primary);
        box-shadow: 5px 5px 5px rgba(0,0,0,0.7);
        border-radius: 5px;
        text-align: left; // desktop
        padding: 20px 15px 10px;
        margin-left: 1rem;
        margin-right: 1rem;
        top: 200px;
        min-height: 261px; // Refine this for tablet (to fit the parent container)
        
    } 

    // when nothig is selected 
    .TM_container.TM_container--no-selection & {

    }

    // when there is a selection: List of programmes / Country selected 
    .TM_container--selection & {

        // The panel becomes bigger convering the maximun
        @media only screen and (min-width: 600px) {
            transform: none;
            top: 80px;
            height: auto;
            max-height: 523px;
            }
        
        .TM_card {
          // doesnt need anything
        }
        .TM_card-body { // this cointainer scrolls if content is too big
            padding-bottom: 10px;
            margin-bottom: 15px;
            overflow-y: auto; // scroll only if needed
            @media only screen and (min-width: 600px) {
                max-height: 350px;
            }
        }

    } // particularities of selections
    // selection country: // when a country is selected the heading says more things and the height is smaller
    @media only screen and (min-width: 600px) {
        .TM_container.country-selected & {
            .TM_card-body {
                max-height: 315px; 
            }
        }
    }

