        //     max-width: 50%;
        //     // overflow-x: scroll ;
        // }
    }

    // When selected (list of programmes / country     


}

// inside the left panel 
.TM_Panel {
    position: absolute;
    z-index: 9;
    background-color: rgba(255,255,255,0.8);
    padding: 1rem 2rem;
    border-radius: 5px;
    max-width: 150px;
    &--hoveredregion {
        left: auto;
        right: 0;

        .region-title {
            font-size: 1.3rem;
            margin: 0;
            line-height: 1;
        }
    }
}




    // when selected button Show Listo fo Programmes 
    ul.TM_List-of-programmes {

        .TM_container.showing-programmes-selected.programme-selected & {
            display: none;   // if the programme is selected, the list of programmes is hidden
        }

        list-style: none;
        padding: 0;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr; // 3 cols
        > li {
            cursor: pointer;
            text-align: center;
            align-items: start;
            display: grid;
            border-bottom: 1px solid $secondary;
            padding: 5px 10px;
            margin-bottom: 10px;
            grid-template-columns: auto; // 1 col
            &:hover {
                background-color: $info;
            }
            &.selected {
                background-color: $info;
            }
            p { 
                font-size: 0.8rem;
                line-height: 1rem;
                margin: 0; 
            }
            img.logo-programme {
                margin: auto;
                max-width: 50px;
            }
            
        }

        
    }

    .InnerPanel-list-of-projects {
        
        .TM_btn-close {

        }
    }



    .TM_list-of-projects {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        grid-gap: 1px;
        list-style: none;
        padding: 0;
        margin: 0;
        grid-auto-flow: row;
        grid-auto-columns: 100px;
        > li {
            cursor: pointer;
            background-color: var(--tm-gray);
            box-shadow: 2px 3px 3px rgb(0 0 0 / 50%);
            .tm_img-wrapper {
                border: 2px solid salmon;
                height: 68px;
                overflow: hidden;
                img {
                    margin: auto;
                    width: 100%;
                }
            }
            .project-title {
                text-align: center;
                width: 100%;
                font-size: 0.8rem;
                line-height: 1rem;
                p { margin: 0; word-break: break-word;}
                &.long-title-5 .sub-title { display: none; } // if title is too long the subtitle not shown.
            }
            border: 2px solid transparent;
            &:hover {
                border: 2px solid $secondary;
                background-color: var(--tm-info);
            }

            // particularities of olours depending on the thematic 
            &.project-environment .tm_img-wrapper {
                border-color: var(--tm-environment);
            }
            &.project-p2p .tm_img-wrapper {
                border-color: var(--tm-p2p);
            }
            &.project-infrastructures .tm_img-wrapper {
                border-color: var(--tm-infrastructure);
            }
            &.project-economical .tm_img-wrapper {
                border-color: var(--tm-economic);
            }
        }
    }

// btn close,on the top right of the panel
.tm_btn-wrapper {
    position: absolute;
    background-color: var(--tm-light);
    right: -25px;
    top: -25px;
    z-index: 9;
    padding: 25px;
    border: 4px solid $primary;
    border-radius: 50%;
    display: flex;
    justify-self: center;
    align-items: center;
    cursor: pointer;
    &:hover {
        background-color: $dark;
        button { color: white; }

        .TM_btn-close {
            font-size: 2rem;
            padding: 3px 5px;
            border-radius: 50%;
            background: none;
        }
    }
    button {
        cursor: pointer;
        position: absolute;
        transform: translate(-50%, -10%);
        border: 0;
        background-color: transparent;
    }
}


.TM_character-over-left-panel {
    display:none;
    @media only screen and (min-width: 600px) {
        display: flex;
        position: absolute;
        left: 100px;
        width: 150px;
        z-index: 10;
        transition: opacity 3s;
        opacity: 1;
        img {
            width: 100%;
            margin-top: 49px;
        }
    }

    .TM_container--active-state & {
        z-index: 1;
        transition: opacity 0.2s;
        opacity: 0;
    }
}

/* adorno bajo el left panel */
.TM_below-left-panel {
    display: none;
    @media only screen and (min-width: 600px) {
        position: absolute;
        // display: block;  // DEACTIVATED 
        bottom: 8%;
        left: 15%;
        z-index: 6;
        img { width: 200px; }
        h3 { margin-top: -30px; text-align:  center;}
    }
}